import {
    Box,
    Button,
    Card,
    Center,
    Container,
    Group,
    Loader,
    LoadingOverlay,
    ScrollArea,
    Stack,
    Table,
    Tabs,
    Text,
} from "@mantine/core";
import {
    IconBasket,
    IconCalendar,
    IconForklift,
    IconHome,
    IconPencil,
    IconPlus,
    IconRecharging,
    IconTool,
    IconTrash,
} from "@tabler/icons-react";
import { useState } from "react";
import NewMeasurementModal from "../components/new-measurement";
import moment from "moment";
import "moment/locale/es"; // without this line it didn't work
moment.locale("es");
const medidores = [
    {
        id: "casa",
        label: "Casa",
        icon: <IconHome />,
    },
    {
        id: "verduleria",
        label: "Verduleria",
        icon: <IconBasket />,
    },
    {
        id: "taller",
        label: "Taller",
        icon: <IconTool />,
    },
    {
        id: "marmoleria",
        label: "Marmoleria",
        icon: <IconForklift />,
    },
];

const LoadValuesPage = ({
    measurements,
    saveMeasurement,
    removeMeasurement,
    loading,
    buttons,
}) => {
    const [setMeasurementType, setSetMeasurementType] = useState();
    const [toEdit, setToEdit] = useState();

    const setMeasurement = (medidorId, measurementToEdit) => {
        setSetMeasurementType(medidorId);
        setToEdit(measurementToEdit);
        console.log(medidorId);
    };

    const closeMeasurementModal = (measurement) => {
        setSetMeasurementType(undefined);
        console.log(measurement);
        if (measurement) {
            // agrego la nueva medicion
            saveMeasurement(measurement);
        }
    };

    return (
        <>
            <NewMeasurementModal
                isOpen={setMeasurementType}
                type={setMeasurementType}
                _measurement={toEdit}
                onClose={closeMeasurementModal}
            />
            <Card shadow="md" withBorder bg={"#fafafa"}>
                <Stack>
                    <Text fw={700}>Mediciones</Text>
                    <Tabs
                        defaultValue={medidores[0].id}
                        styles={{ list: { flexWrap: "nowrap" } }}
                    >
                        <ScrollArea scrollbarSize={0}>
                            <Tabs.List>
                                {medidores.map((medidor) => (
                                    <Tabs.Tab
                                        key={medidor.id}
                                        value={medidor.id}
                                        leftSection={medidor.icon}
                                    >
                                        {medidor.label}
                                    </Tabs.Tab>
                                ))}
                            </Tabs.List>
                        </ScrollArea>
                        {medidores.map((medidor) => (
                            <Tabs.Panel key={medidor.id} value={medidor.id}>
                                <Stack p={"sm"}>
                                    <Group>
                                        <Button
                                            leftSection={<IconPlus />}
                                            onClick={() =>
                                                setMeasurement(medidor.id, null)
                                            }
                                        >
                                            Agregar
                                        </Button>
                                    </Group>
                                    <ScrollArea h={150} pr={20} bg={"#f2f2f2"}>
                                        <Table>
                                            <Table.Tbody>
                                                {measurements
                                                    .filter(
                                                        (m) =>
                                                            m.medidorId ===
                                                            medidor.id
                                                    )
                                                    .sort(
                                                        (a, b) =>
                                                            moment(b.date)
                                                                .toDate()
                                                                .getTime() -
                                                            moment(a.date)
                                                                .toDate()
                                                                .getTime()
                                                    )
                                                    .map((m, idx) => (
                                                        <Table.Tr key={idx}>
                                                            <Table.Td
                                                                w={0}
                                                                visibleFrom="xs"
                                                            >
                                                                <IconRecharging />
                                                            </Table.Td>
                                                            <Table.Td>
                                                                <Text>
                                                                    {m.value}
                                                                    kWh
                                                                </Text>
                                                            </Table.Td>
                                                            <Table.Td
                                                                w={0}
                                                                visibleFrom="xs"
                                                            >
                                                                <IconCalendar />
                                                            </Table.Td>
                                                            <Table.Td>
                                                                <Text
                                                                    styles={{
                                                                        root: {
                                                                            whiteSpace:
                                                                                "nowrap",
                                                                        },
                                                                    }}
                                                                >
                                                                    {moment(
                                                                        m.date
                                                                    ).format(
                                                                        "DD MMMM YYYY"
                                                                    )}
                                                                </Text>
                                                            </Table.Td>
                                                            <Table.Td w={0}>
                                                                <Group wrap="nowrap">
                                                                    <Button
                                                                        p={0}
                                                                        variant="transparent"
                                                                        onClick={() =>
                                                                            setMeasurement(
                                                                                medidor.id,
                                                                                m
                                                                            )
                                                                        }
                                                                        px={
                                                                            "xs"
                                                                        }
                                                                    >
                                                                        <IconPencil />
                                                                    </Button>
                                                                    <Button
                                                                        p={0}
                                                                        variant="transparent"
                                                                        onClick={() =>
                                                                            removeMeasurement(
                                                                                m.id
                                                                            )
                                                                        }
                                                                        px={
                                                                            "xs"
                                                                        }
                                                                    >
                                                                        <IconTrash />
                                                                    </Button>
                                                                </Group>
                                                            </Table.Td>
                                                        </Table.Tr>
                                                    ))}
                                            </Table.Tbody>
                                        </Table>
                                    </ScrollArea>
                                    {buttons}
                                </Stack>
                            </Tabs.Panel>
                        ))}
                    </Tabs>
                </Stack>
            </Card>
        </>
    );
};

export default LoadValuesPage;
