import {
    Alert,
    Box,
    Button,
    Card,
    Center,
    Grid,
    Group,
    Stack,
    Text,
    TextInput,
} from "@mantine/core";
import {
    IconExclamationCircle,
    IconExclamationMark,
} from "@tabler/icons-react";
import { useState } from "react";

export const PinInpt = ({ onSetPin, errorMsg }) => {
    const [pin, setPin] = useState("");

    return (
        <Stack align="center">
            <Card shadow="md" withBorder bg={"#fafafa"} w={300}>
                <Grid>
                    {errorMsg && (
                        <Grid.Col span={12}>
                            <Alert
                                title="Error"
                                color="red"
                                w={"100%"}
                                icon={<IconExclamationCircle />}
                            >
                                {errorMsg}
                            </Alert>
                        </Grid.Col>
                    )}

                    <Grid.Col span={12}>
                        <Box bg={"#eaeaea"} h={26}>
                            <Text>{pin}</Text>
                        </Box>
                    </Grid.Col>

                    {[1, 2, 3, 4, 5, 6, 7, 8, 9].map((num) => (
                        <Grid.Col span={4} key={num}>
                            <Button onClick={() => setPin(pin + num)} fullWidth>
                                {num}
                            </Button>
                        </Grid.Col>
                    ))}
                    <Grid.Col span={4}>
                        <Button
                            color="red"
                            onClick={() => setPin("")}
                            fullWidth
                        >
                            Borrar
                        </Button>
                    </Grid.Col>
                    <Grid.Col span={4}>
                        <Button onClick={() => setPin(pin + "0")} fullWidth>
                            0
                        </Button>
                    </Grid.Col>
                    <Grid.Col span={4}>
                        <Button
                            color="green"
                            onClick={() => onSetPin(pin)}
                            fullWidth
                        >
                            OK
                        </Button>
                    </Grid.Col>
                </Grid>
            </Card>{" "}
        </Stack>
    );
};
